import { useEffect, useState, Fragment } from "react";
import { Helmet } from "react-helmet";
import { Hidden } from "@material-ui/core";

import ThickArrow from "../../assets/icons/thickArrow.svg";
import Header from "../../Components/Header/SignUpSetup";
import { UIButton } from "../../Components/exports";
import NewSetupWhatsappChatbot from "../../Components/NewSetupWhatsappChatbot";
import NewSetupSocialScheduling from "../../Components/NewSetupSocialScheduling";
import { useToastMessage } from "../../contexts/ToastMessageContext";

import image2 from "./assets/image-2.svg";
import EngageImg from "./assets/engage.svg";
import CurateImg from "./assets/curate.svg";

// styles
import useStyles from "./styles/muiStyles";

// query string
import queryString from "query-string";

// react-router-dom
import { useHistory, withRouter } from "react-router-dom";

//import { getBusinessById } from "../../api/Dashboard";
import { getBusinessById } from "../../api/preBusiness";
import { getActivePlan } from "../../api/Business";

// backend imports
import { FE_REDIRECT_URL } from "../../Backend";
import { ConsoleView } from "react-device-detect";

const GettingStarted = ({ location }) => {
  const { setErrorMessage } = useToastMessage();
  const [title, setTitle] = useState("Getting Started"),
    classes = useStyles(),
    [nextStep, setNextStep] = useState(5),
    [planCategory, setPlanCategory] = useState([]),
    [accountType] = useState(1),
    [platform, setPlatform] = useState(null),
    [TWUser, setTWUser] = useState(null),
    [LIUser, setLIUser] = useState(null),
    setupWhatsapp = () => {
      setNextStep(6);
      setTitle("Setup WhatsApp Chatbot");
    },
    setupSocialScheduling = () => {
      setNextStep(7);
      setTitle("Setup Social Scheduling");
    };

  // history
  const history = useHistory();

  useEffect(() => {
    const qs = queryString.parse(location.search);
    const { TWUser, LIUser, bid, twAuthError, liAuthError } = qs;

    const replaceURL = `${FE_REDIRECT_URL}/getting-started`;

    if (twAuthError) {
      const esUpdateTWAccount = localStorage.getItem("esUpdateTWAccount");

      if (esUpdateTWAccount === "true") {
        localStorage.removeItem("esUpdateTWAccount");

        return history.push(
          `/business/social-linking?twAuthError=${twAuthError}`
        );
      }

      return setErrorMessage(twAuthError);
    }

    if (liAuthError) {
      const esUpdateLIAccount = localStorage.getItem("esUpdateLIAccount");

      if (esUpdateLIAccount === "true") {
        localStorage.removeItem("esUpdateLIAccount");

        return history.push(
          `/business/social-linking?liAuthError=${liAuthError}`
        );
      }

      return setErrorMessage(liAuthError);
    }

    if (TWUser) {
      const esUpdateTWAccount = localStorage.getItem("esUpdateTWAccount");

      if (esUpdateTWAccount === "true") {
        const twUser = JSON.parse(TWUser);

        localStorage.removeItem("esUpdateTWAccount");

        history.push(
          `/business/social-linking?twUser=${JSON.stringify(twUser)}`
        );
      } else {
        setTWUser(TWUser);
        setPlatform("twitter");
        setNextStep(7);

        window.history.replaceState({}, null, replaceURL);
      }
    }

    if (LIUser) {
      const esUpdateLIAccount = localStorage.getItem("esUpdateLIAccount");

      if (esUpdateLIAccount === "true") {
        const liUser = JSON.parse(LIUser);

        localStorage.removeItem("esUpdateLIAccount");

        // redirect to business update page with twUser string
        history.push(
          `/business/social-linking?liUser=${JSON.stringify(liUser)}`
        );
      } else {
        //const liUser = JSON.parse(LIUser);

        setLIUser(LIUser);
        setPlatform("linkedin");
        setNextStep(7);

        // window.history.replaceState({}, null, replaceURL); // comment by nagendra
      }
    }
    // eslint-disable-next-line
  }, [history, location.search]);

  useEffect(() => {
    //localStorage.removeItem("selectedPlanDetail");
    loadBusinessDetail();
  }, []);

  const loadBusinessDetail = async () => {
    const businessId = localStorage.getItem("businessId");
    const res = await getBusinessById(businessId);
    const activePlans = await getActivePlan();

    if (!activePlans.success) return;
    //const plans = JSON.parse(localStorage.getItem("selectedPlanDetail"));

    if (activePlans.payload.length === 1) {
      let catName = activePlans.payload[0]["product"];
      if (catName === "curate") {
        setNextStep(7);
        setTitle("Setup Social Scheduling");
      } else if (catName === "engage") {
        setNextStep(6);
        setTitle("Setup WhatsApp Chatbot");
      }
    } else {
      setPlanCategory(activePlans.payload);
    }

    /*if (!res.error) {
      if(res.data.plan_category.length===1){
        let catName = res.data.plan_category[0];
        if(catName==='curate'){
          setNextStep(7);
          setTitle("Setup Social Scheduling");
        } else if(catName==='engage'){
          setNextStep(6);
          setTitle("Setup WhatsApp Chatbot");
        }
      }
      setPlanCategory(res.data.plan_category);
    } */
  };

  return (
    <Fragment>
      {/* Head */}
      <Helmet>
        <title>Getting Started - EasySocial.io</title>
        <link rel="canonical" href="https://easysocial.io/" />
        <meta
          name="keywords"
          content="Whatsapp chatbot ,automatic WhatsApp for business, Whatsapp AI, customizable whatsapp"
        />
        <meta
          name="description"
          content="EasySocial offers customizable and affordable WhatsApp chatbot solution for quick and hassle free functioning of your business."
        />
      </Helmet>
      <div className={classes["setup-process"]}>
        <Header />
        <section className={classes.setup}>
          <div className="content">
            <Hidden smDown implementation="js">
              <div className="block-left">
                <div className="block-fixed">
                  <h1
                    dangerouslySetInnerHTML={{
                      __html:
                        "Sign Up Successful ! <br />Enter your business details <br />to start your journey.",
                    }}
                  />
                  <img src={image2} alt="image2" className="block-img" />
                </div>
              </div>
            </Hidden>
            <div className="block-right">
              <div className="getting-started">
                <div className="col-10 col-sm-6 col-md-12">
                  {title !== "" && (
                    <div className="header">
                      <Hidden mdUp implementation="js">
                        <button type="button" className="go-back">
                          <img src={ThickArrow} alt="back button" />
                        </button>
                      </Hidden>
                      <h4 className="heading">{title}</h4>
                    </div>
                  )}
                  <div className="actions">
                    {nextStep === 5 && (
                      <Fragment>
                        {planCategory &&
                          planCategory.map((plan) => {
                            return (
                              <div className="product">
                                <div className="icon">
                                  <img
                                    src={
                                      plan.product === "engage"
                                        ? EngageImg
                                        : CurateImg
                                    }
                                    alt={plan.product}
                                  />
                                </div>
                                <div className="subtitle">
                                  <strong>
                                    {plan.product === "engage"
                                      ? "WhatsApp Chatbot"
                                      : "Social Media Scheduling"}
                                  </strong>
                                </div>
                                <div className="text">
                                  {plan.product === "engage"
                                    ? "Start communicating with your customers on whatsapp"
                                    : "Manage and schedule your social media posts on Facebook, Instagram, Twitter & LinkedIn"}
                                </div>
                                <UIButton
                                  variant="contained"
                                  className="btn-setup"
                                  onClick={
                                    plan.product === "engage"
                                      ? setupWhatsapp
                                      : setupSocialScheduling
                                  }
                                >
                                  {plan.product === "engage"
                                    ? "Setup WhatsApp Chatbot"
                                    : "Setup Social Scheduling"}
                                </UIButton>
                              </div>
                            );
                          })}
                      </Fragment>
                    )}
                    {nextStep === 6 && accountType === 1 && (
                      <NewSetupWhatsappChatbot />
                    )}
                    {nextStep === 7 && accountType === 1 && (
                      <NewSetupSocialScheduling
                        setTitle={setTitle}
                        setNextStep={setNextStep}
                        TWUser={TWUser}
                        setTWUser={setTWUser}
                        LIUser={LIUser}
                        setLIUser={setLIUser}
                        platform={platform}
                        setPlatform={setPlatform}
                      />
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </Fragment>
  );
};

export default withRouter(GettingStarted);
