/* eslint-disable jsx-a11y/anchor-is-valid */

import React, { Fragment, useState, useEffect } from "react";
import {
  Button,
  List,
  ListItemAvatar,
  ListItemIcon,
  ListItemText,
  Divider,
} from "@material-ui/core";
import {
  Checkbox,
  ListItem,
  ListItemButton,
  FormControlLabel,
  Skeleton,
} from "@mui/material";
import {
  Check,
  LinkedIn,
  CircleOutlined,
  CheckCircleOutlined,
  ThumbUp,
} from "@mui/icons-material";

// import { UIButton } from "../../exports";

// import Page1Img from "../assets/page-1.svg";
// import Page2Img from "../assets/page-2.svg";

// API imports
import { API, API_V2 } from "../../../Backend";

// requests imports
import {
  fetchLiUserPages,
  setupLinkedinBusiness,
} from "../../../api/SocialMediaSetup";

// toast notifications
import { useToastMessage } from "../../../contexts/ToastMessageContext";

import CircularProgress from "@mui/material/CircularProgress";

const LinkedInLogin = ({
  className,
  step,
  setHead,
  setStep,
  continueTo,
  skipPlatFormTo,
  goToFinalStep,
  LIUser,
  toLinkOnlyAccount,
}) => {
  const [pages, setPages] = useState([
      // {
      //   image: Page1Img,
      //   name: "Honda bikes",
      //   selected: true,
      //   inUse: true,
      // },
      // {
      //   image: Page2Img,
      //   name: "Jaipur Photography",
      //   selected: false,
      //   inUse: false,
      // },
    ]),
    [selectedPage, setSelectedPage] = useState(null),
    [showLoading, setShowLoading] = useState(false),
    [noUserOrgPagesFound, setNoUserOrgPagesFound] = useState(false),
    // selectPage = (selectedIndex, pageInfo) => {
    //   setPages((prev) =>
    //     prev.map((page, index) =>
    //       selectedIndex === index
    //         ? { ...page, selected: !page.selected }
    //         : { ...page }
    //     )
    //   );

    //   setSelectedPage(pageInfo);
    // };
    selectPage = (selectedIndex) => {
      setPages((prev) =>
        prev.map((page, index) => {
          if (selectedIndex === index) {
            if (page.selected) {
              setSelectedPage(null);
            } else {
              setSelectedPage(page);
            }

            return { ...page, selected: !page.selected };
          } else {
            return { ...page };
          }
        })
      );
    };

  console.log(step);

  // contexts
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  // component effects
  useEffect(() => {
    if (LIUser) {
      // fetch user organizations
      const liUserObject = JSON.parse(LIUser);

      const liToken = liUserObject.token;

      // Add loader
      setShowLoading(true);

      fetchLiUserPages(liToken)
        .then((res) => {
          console.log("data", res);
          const { payload } = res;

          if (!res.success) {
            setErrorMessage("No user linkedin organization pages found!");
            setNoUserOrgPagesFound(true);
          } else {
            // render the pages in the UI by setting state
            const liOrgData = payload.map((org) => {
              return {
                orgID: org.id,
                image: org.logo,
                name: org.name,
                selected: false,
                inUse: false,
              };
            });

            setShowLoading(false);
            setPages(liOrgData);
          }
        })
        .catch(() => {
          setErrorMessage("Failed to fetch organization data!");
          setShowLoading(false);
        });
    }
  }, [LIUser]);

  useEffect(() => {
    if (pages.length) {
      setStep(1);
    }
  }, [pages, setStep]);

  // Event handlers

  const handleLILoginRedirect = async () => {
    // setStep(1);
    // setHead("Twitter Account is Linked");

    // window.open("http://localhost:5000/api/linkedin/login", "_self");
    window.open(`${API_V2}/setup/linkedin/login`, "_self");
  };

  const handleLIConnect = async (event) => {
    event.preventDefault();

    if (!selectedPage) {
      return setErrorMessage("Please select an organization first!");
    }

    const { token, tokenSecret, id } = JSON.parse(LIUser);

    const liRequestData = {
      orgID: selectedPage.orgID,
      orgLogo: selectedPage.image,
      orgName: selectedPage.name,
      token: token,
      //tokenSecret: tokenSecret,
      linkedinUserId: id,
    };
    try {
      const { success } = await setupLinkedinBusiness(liRequestData);

      if (success) {
        // call on success
        goToFinalStep();
        setSuccessMessage("LinkedIn setup complete!");
      }
    } catch (error) {
      const { message, error: err } = error;

      if (message) {
        setErrorMessage(message);
      } else if (err) {
        setErrorMessage(err);
      } else {
        setErrorMessage(error);
      }
    }
  };

  const ListItemImage = ({ src, alt }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => {
      setLoading(false);
    };

    return (
      <ListItemAvatar>
        {loading && <Skeleton variant="rectangular" width={50} height={50} />}
        {src && (
          <img
            src={src}
            alt={alt}
            onLoad={onImageLoad}
            width={loading ? 0 : 50}
            height={loading ? 0 : 50}
            style={{ maxWidth: "100%" }}
          />
        )}
      </ListItemAvatar>
    );
  };

  return (
    <div className={className}>
      {showLoading && <CircularProgress />}
      {step === 0 && (
        <div className="platform-head">
          <p>
            {
              "This will allow you create, schedule & manage posts directly from easysocial."
            }
          </p>
          <a target="_blank" rel="noopener noreferrer">
            {"View more details"}
          </a>
        </div>
      )}
      {step === 1 && (
        <List>
          {pages.length > 0 &&
            pages.map(({ image, name, selected, inUse, orgID }, index) => (
              <Fragment key={index}>
                <ListItem sx={{ p: 0 }}>
                  <ListItemButton
                    onClick={() =>
                      // selectPage(index, { image, name, selected, inUse, orgID })
                      selectPage(index)
                    }
                  >
                    <ListItemImage src={image} alt={name} />
                    <ListItemText
                      primary={name}
                      secondary={inUse ? "Page already in use" : undefined}
                    />
                    <ListItemIcon>
                      {selected ? (
                        <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                      ) : (
                        <CircleOutlined sx={{ color: "#24272C" }} />
                      )}
                    </ListItemIcon>
                  </ListItemButton>
                </ListItem>
                {index !== pages.length - 1 && <Divider />}
              </Fragment>
            ))}
        </List>
      )}

      {noUserOrgPagesFound && (
        <>
          <p>
            Sorry, we could not find any LinkedIn organization page associated
            with your account.
          </p>
          <p>
            You may create a an organization and connect it with your EasySocial
            account later from settings menu.
          </p>
        </>
      )}

      {step === 0 && (
        <Button className="btn-linkedin" onClick={handleLILoginRedirect}>
          <LinkedIn sx={{ color: "#ffffff" }} />
          <span className="label">{"Sign in with LinkedIn"}</span>
        </Button>
      )}
      {false && step === 1 && (
        <FormControlLabel
          control={<Checkbox />}
          label="Link my Linkedin Profile"
        />
      )}
      <div className="actions">
        {step === 1 && (
          <Button
            variant="contained"
            className="btn-next"
            style={{
              marginTop: "40px",
              marginBottom: "30px",
              marginLeft: 0,
            }}
            onClick={handleLIConnect}
          >
            <ThumbUp sx={{ color: "#ffffff" }} />
            {"Finish social setup"}
          </Button>
        )}
        <a className="skip" onClick={() => skipPlatFormTo(null)}>
          <span className="label">{"Skip Linkedin"}</span>
          <svg
            width="6"
            height="10"
            viewBox="0 0 6 10"
            fill="none"
            xmlns="//www.w3.org/2000/svg"
          >
            <path
              opacity="0.7"
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.483211 0.526985C0.221976 0.811968 0.241229 1.25477 0.526212 1.516L3.96329 4.66666L0.526212 7.81732C0.241229 8.07855 0.221976 8.52135 0.483211 8.80633C0.744446 9.09132 1.18724 9.11057 1.47223 8.84933L5.47223 5.18267C5.61687 5.05008 5.69922 4.86287 5.69922 4.66666C5.69922 4.47044 5.61687 4.28324 5.47223 4.15065L1.47223 0.483984C1.18724 0.222749 0.744446 0.242001 0.483211 0.526985Z"
              fill="#24272C"
            />
          </svg>
        </a>
      </div>
    </div>
  );
};

export default LinkedInLogin;
