/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Fragment, useState, useEffect } from "react";
import {
  List,
  ListItemAvatar,
  ListItemIcon,
  ListItemText,
  Divider,
} from "@material-ui/core";
import { ListItem, ListItemButton, Skeleton, Link } from "@mui/material";

import { CircleOutlined, CheckCircleOutlined } from "@mui/icons-material";

import { UIButton } from "../../exports";

// import Page1Img from "../assets/page-1.svg";
// import Page2Img from "../assets/page-2.svg";

import {
  fetchBusinessFBPages,
  saveFBPageData,
} from "../../../api/SocialMediaSetup";

// toast notifications
import { useToastMessage } from "../../../contexts/ToastMessageContext";

const SelectFacebookPage = ({
  step,
  className,
  setHead,
  setStep,
  continueTo,
  skipPlatFormTo,
  toLinkOnlyAccount,
}) => {
  const [pages, setPages] = useState([
      // {
      //   image: Page1Img,
      //   name: "Honda bikes",
      //   selected: true,
      //   inUse: true,
      // },
      // {
      //   image: Page2Img,
      //   name: "Jaipur Photography",
      //   selected: false,
      //   inUse: false,
      // },
    ]),
    [selectedPage, setSelectedPage] = useState(null),
    [selectedPages, setSelectedPages] = useState(0),
    [noUserPagesFoundError, setNoUserPagesFoundError] = useState(false),
    // selectPage = (selectedIndex, page) => {
    //   setPages((prev) =>
    //     prev.map((page, index) =>
    //       selectedIndex === index
    //         ? { ...page, selected: !page.selected }
    //         : { ...page }
    //     )
    //   );
    //   setSelectedPage(page);
    // };

    // selectPage = (selectedIndex, fbPage) => {
    selectPage = (selectedIndex) => {
      setPages((prev) =>
        prev.map((page, index) => {
          if (selectedIndex === index) {
            if (page.selected) {
              if (page.inUse) {
                setErrorMessage("Page already in use!");
                setSelectedPage(null);
              } else {
                setSelectedPages((preVal) => preVal - 1);
                setSelectedPage(null);
              }
            } else {
              setSelectedPages((preVal) => preVal + 1);
              setSelectedPage(page);
            }
            return { ...page, selected: !page.selected };
          } else {
            return { ...page };
          }
        })
      );
    };

  // contexts
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  // Component Effects
  useEffect(() => {
    loadFacebookPages();
  }, []);

  const loadFacebookPages = async () => {
    const res = await fetchBusinessFBPages();

    try {
      if (!res.success) {
        setErrorMessage("No user facebook pages found!");
        setNoUserPagesFoundError(true);
      } else {
        const { payload: fb_pages } = res;

        const fbPagesData = fb_pages.map((page) => {
          return {
            pageID: page.page_id,
            image: page.profile_image_url,
            name: page.page_name,
            selected: false,
            inUse: page.isUsedByBusiness,
          };
        });

        setPages(fbPagesData);
      }
    } catch (err) {
      console.log(err);
      setErrorMessage("Failed to fetch facebook pages!");
    }
  };

  // Event handlers
  const saveFaceBookPageData = async (status) => {
    if (!selectedPage) {
      return setErrorMessage("Please select a facebook page first!");
    }

    // save the page data
    const page = {
      img_url: selectedPage.image,
      page_id: selectedPage.pageID,
      page_name: selectedPage.name,
    };

    try {
      const res = await saveFBPageData(page);

      // TODO: Change the state to show Instagram setup

      if (res.success) {
        setSuccessMessage("Facebook setup complete!");
        if (status === "next") {
          setHead("Select Instagram Account");
          continueTo("instagram");
        } else {
          skipPlatFormTo("twitter");
        }
      }
    } catch (error) {
      const { message, error: err } = error;

      if (message) {
        setErrorMessage(message);
      } else if (err) {
        setErrorMessage(err);
      } else {
        setErrorMessage(error);
      }
    }
  };

  const ListItemImage = ({ src, alt }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => {
      setLoading(false);
    };

    return (
      <ListItemAvatar>
        {loading && <Skeleton variant="rectangular" width={50} height={50} />}
        <img
          src={src}
          alt={alt}
          onLoad={onImageLoad}
          width={loading ? 0 : 50}
          height={loading ? 0 : 50}
        />
      </ListItemAvatar>
    );
  };

  return (
    <div className={className}>
      <List>
        {pages.length > 0 &&
          pages.map(({ image, name, selected, inUse, pageID }, index) => (
            <Fragment key={index}>
              <ListItem sx={{ p: 0 }}>
                <ListItemButton onClick={() => selectPage(index)}>
                  <ListItemImage src={image} alt={name} />
                  <ListItemText
                    primary={name}
                    secondary={inUse ? "Page already in use" : undefined}
                  />
                  <ListItemIcon>
                    {selected || inUse ? (
                      <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                    ) : (
                      <CircleOutlined sx={{ color: "#24272C80" }} />
                    )}
                  </ListItemIcon>
                </ListItemButton>
              </ListItem>
              {index !== pages.length - 1 && <Divider />}
            </Fragment>
          ))}

        {noUserPagesFoundError && (
          <>
            <p>
              Sorry, we couldn't find any Facebook page associated with your
              account.
            </p>
            <p>
              You may create a page and connect it with your EasySocial account
              later from settings menu.
            </p>
          </>
        )}
      </List>
      <div className="actions">
        <UIButton
          variant="contained"
          className="btn-next"
          style={{
            marginTop: "40px",
            marginBottom: "30px",
            marginLeft: 0,
          }}
          onClick={() => saveFaceBookPageData("next")}
          disabled={selectedPages === 0}
        >
          {"Link & Continue to Instagram"}
        </UIButton>
        <Link
          className="skip"
          component="button"
          // onClick={() => saveFaceBookPageData("skip")}
          onClick={() => skipPlatFormTo("twitter")}
        >
          <span className="label">{"Skip Instagram"}</span>
          <svg
            width="6"
            height="10"
            viewBox="0 0 6 10"
            fill="none"
            xmlns="//www.w3.org/2000/svg"
          >
            <path
              opacity="0.7"
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.483211 0.526985C0.221976 0.811968 0.241229 1.25477 0.526212 1.516L3.96329 4.66666L0.526212 7.81732C0.241229 8.07855 0.221976 8.52135 0.483211 8.80633C0.744446 9.09132 1.18724 9.11057 1.47223 8.84933L5.47223 5.18267C5.61687 5.05008 5.69922 4.86287 5.69922 4.66666C5.69922 4.47044 5.61687 4.28324 5.47223 4.15065L1.47223 0.483984C1.18724 0.222749 0.744446 0.242001 0.483211 0.526985Z"
              fill="#24272C"
            />
          </svg>
        </Link>
      </div>
    </div>
  );
};

export default SelectFacebookPage;
