/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Fragment, useState } from "react";
import { Button, IconButton } from "@material-ui/core";

import {
  Facebook,
  Twitter,
  LinkedIn,
  ArrowBackIosNew,
} from "@mui/icons-material";

// react-router-dom
import { withRouter } from "react-router-dom";

// styles
import useStyles from "./styles/muiStyles";
import FacebookIcon from "./assets/facebook.svg";
import InstagramIcon from "./assets/instagram.svg";
import TwitterIcon from "./assets/twitter.svg";
import LinkedInIcon from "./assets/linkedin.svg";

import SelectFacebookPage from "./SelectFacebookPage";
import SelectInstagramAccount from "./SelectInstagramAccount";
import TwitterLogin from "./TwitterLogin";
import LinkedInLogin from "./LinkedInLogin";
import FinalStep from "./FinalStep";

// facebook login component
// import FacebookLogin from "react-facebook-login";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";

// setup request helpers imports
import { saveFBUserData } from "../../api/SocialMediaSetup";

// toast notifications
import { useToastMessage } from "../../contexts/ToastMessageContext";

const NewSetupSocialScheduling = ({
  setTitle,
  setNextStep,
  location,
  TWUser,
  setTWUser,
  LIUser,
  setLIUser,
  platform,
  setPlatform,
}) => {
  const classes = useStyles(),
    // facebookColor = "#4267B2",
    // instagramColor = "#8a3ab9",
    // twitterColor = "#1DA1F2",
    // linkedInColor = "#0077b5",
    // [platform, setPlatform] = useState(null),
    [head, setHead] = useState(""),
    [step, setStep] = useState(0),
    [isFinished, setFinished] = useState(false),
    [toLinkFBIG, setToLinkFBIG] = useState(false),
    [toLinkOnlyAccount, setToLinkOnlyAccount] = useState(false),
    // [platformStatus, setPlatformStatus] = useState({
    //   facebook: false,
    //   instagram: false,
    //   twitter: false,
    //   linkedin: false,
    // }),
    goBack = () => {
      setPlatform(null);
      setTitle("Setup Social Scheduling");
      setHead("");
      setStep(0);
    },
    continueTo = (platformName) => {
      setPlatform(platformName);
      setStep(0);
    },
    goToFinalStep = () => {
      setTitle("Setup Social Scheduling");
      setFinished(true);
      setHead("");
      setPlatform(null);
      setStep(0);
    },
    skipPlatFormTo = (platformName) => {
      if (platformName === null) {
        goToFinalStep();
        return;
      }
      setTitle("Setup Social Scheduling");
      setHead("");
      setPlatform(platformName);
      setStep(0);
    };

  !false && console.log(platform, step);

  // contexts
  const { setErrorMessage } = useToastMessage();

  // Event handlers
  const HandleFBResponse = async (response) => {
    console.log("response", response);
    // response validation
    if (
      !("accessToken" in response) &&
      !("id" in response) &&
      !("name" in response)
    ) {
      // login error
      setErrorMessage("Problem logging in with Facebook!");
      return;
    }

    const fbData = {
      //businessId: localStorage.getItem("businessId"),
      businessId: localStorage.getItem("businessId"),
      fb_user_id: response.id,
      short_lived_token: response.accessToken,
    };

    try {
      const setupFBData = await saveFBUserData(fbData);

      const { success } = setupFBData.data;

      if (success) {
        setPlatform("facebook");
        setHead("Select Facebook Page");
        setTitle("");
      }
    } catch (error) {
      setErrorMessage(error);
    }
  };

  return (
    <Fragment>
      <div className={classes["entry-header"]}>
        {platform === null ? (
          <Fragment>
            <p>
              {
                "This will allow you create, schedule & manage posts directly from easysocial."
              }
            </p>
            <a target="_blank" rel="noopener noreferrer">
              {"View more details"}
            </a>
          </Fragment>
        ) : (
          <Fragment>
            <div className="link-block">
              <div className={`link-icon ${platform}`}>
                {platform === "facebook" && (
                  <img
                    src={FacebookIcon}
                    alt="Facebook"
                    height={32}
                    width={32}
                  />
                )}
                {platform === "instagram" && (
                  <img
                    src={InstagramIcon}
                    alt="Instagram"
                    height={32}
                    width={32}
                  />
                )}
                {platform === "twitter" && (
                  <img src={TwitterIcon} alt="Twitter" height={32} width={32} />
                )}
                {platform === "linkedin" && (
                  <img
                    src={LinkedInIcon}
                    alt="Twitter"
                    height={32}
                    width={32}
                  />
                )}
              </div>
              <div className="link-label">{`Linking ${platform}`}</div>
            </div>
            <div className="link-head">
              {false && (
                <IconButton onClick={goBack}>
                  <ArrowBackIosNew />
                </IconButton>
              )}
              <h3>{head}</h3>
            </div>
          </Fragment>
        )}
      </div>
      {platform === null && step === 0 && !isFinished && (
        <div className={`${classes["select-platform"]} step-0`}>
          <h3>{"Link Accounts"}</h3>
          <hr />
          <ul>
            <li>
              <p>{"Facebook and Instagram"}</p>
              {/* <Button
                className="btn-facebook"
                onClick={() => {
                  setPlatform("facebook");
                  setHead("Select Facebook Page");
                  setTitle("");
                }}
              >
                <Facebook sx={{ color: "#ffffff" }} />
                <span className="label">{"Login with Facebook"}</span>
              </Button> */}
              <FacebookLogin
                appId={process.env.REACT_APP_APP_ID}
                autoLoad={false}
                fields="name,email,picture"
                scope="pages_manage_cta,pages_read_engagement,pages_manage_posts,ads_management,ads_read,leads_retrieval,instagram_basic,instagram_manage_comments,read_insights,instagram_content_publish,instagram_manage_insights,pages_manage_engagement"
                callback={HandleFBResponse}
                render={(renderProps) => (
                  <Button
                    className="btn-facebook"
                    onClick={() => {
                      renderProps.onClick();
                    }}
                  >
                    <Facebook sx={{ color: "#ffffff" }} />
                    <span className="label">{"Login with Facebook"}</span>
                  </Button>
                )}
              />
            </li>
            {!toLinkFBIG && (
              <>
                <li>
                  <p>{"Twitter"}</p>
                  <Button
                    className="btn-twitter"
                    onClick={() => {
                      setPlatform("twitter");
                      setHead("Login with Twitter");
                      setTitle("");
                    }}
                  >
                    <Twitter sx={{ color: "#ffffff" }} />
                    <span className="label">{"Sign in with Twitter"}</span>
                  </Button>
                </li>
                <li>
                  <p>{"LinkedIn"}</p>
                  <Button
                    className="btn-linkedin"
                    onClick={() => {
                      setPlatform("linkedin");
                      setHead("Login with LinkedIn");
                      setTitle("");
                    }}
                  >
                    <LinkedIn sx={{ color: "#ffffff" }} />
                    <span className="label">{"Sign in with LinkedIn"}</span>
                  </Button>
                </li>
              </>
            )}
          </ul>
        </div>
      )}
      {platform === "facebook" && (
        <Fragment>
          <SelectFacebookPage
            step={step}
            setStep={setStep}
            setHead={setHead}
            className={`${classes["platform"]} ${platform}`}
            continueTo={continueTo}
            skipPlatFormTo={skipPlatFormTo}
            toLinkOnlyAccount={toLinkOnlyAccount}
          />
        </Fragment>
      )}
      {platform === "instagram" && (
        <Fragment>
          <SelectInstagramAccount
            step={step}
            setStep={setStep}
            setHead={setHead}
            className={`${classes["platform"]} ${platform}`}
            continueTo={continueTo}
            skipPlatFormTo={skipPlatFormTo}
            goToFinalStep={goToFinalStep}
            toLinkOnlyAccount={toLinkOnlyAccount}
          />
        </Fragment>
      )}
      {platform === "twitter" && (
        <Fragment>
          <TwitterLogin
            step={step}
            setStep={setStep}
            setHead={setHead}
            className={`${classes["platform"]} ${platform}`}
            continueTo={continueTo}
            skipPlatFormTo={skipPlatFormTo}
            TWUser={TWUser}
            goToFinalStep={goToFinalStep}
            toLinkOnlyAccount={toLinkOnlyAccount}
          />
        </Fragment>
      )}
      {platform === "linkedin" && (
        <Fragment>
          <LinkedInLogin
            step={step}
            setStep={setStep}
            setHead={setHead}
            className={`${classes["platform"]} ${platform}`}
            continueTo={continueTo}
            skipPlatFormTo={skipPlatFormTo}
            goToFinalStep={goToFinalStep}
            LIUser={LIUser}
            toLinkOnlyAccount={toLinkOnlyAccount}
          />
        </Fragment>
      )}
      {platform === null && isFinished && (
        <Fragment>
          <FinalStep
            className={classes["final-step"]}
            setSetupPlatform={setPlatform}
            setStep={setStep}
            setFinished={setFinished}
            setToLinkFBIG={setToLinkFBIG}
            setToLinkOnlyAccount={setToLinkOnlyAccount}
            setTWUser={setTWUser}
            setLIUser={setLIUser}
          />
        </Fragment>
      )}
    </Fragment>
  );
};

export default withRouter(NewSetupSocialScheduling);
