import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  "entry-header": {
    paddingTop: "15px",

    "& > p": {
      color: "#24272C",
      fontSize: "14px",
      fontWeight: 400,
      lineHeight: "20px",
      marginBottom: "6px",
      opacity: 0.7,
    },

    "& > a": {
      color: "#656F7A",
      cursor: "pointer",
      fontSize: "14px",
      fontWeight: 500,
      lineHeight: "16px",

      "&:hover": {
        textDecoration: "underline",
      },
    },

    "& .link-block": {
      alignItems: "center",
      display: "inline-flex",
      justifyContent: "center",
      paddingRight: "16px",
      columnGap: "14px",
      marginBottom: "36px",

      "& .link-icon": {
        borderRadius: "6px",
        boxShadow:
          "inset 0 0 0 3px #12B0EE, 0px 0px 6px rgba(50, 197, 255, 0.5)",
        display: "flex",
        overflow: "hidden",
        padding: "4px",

        "&.instagram": {
          borderRadius: "12px",
        },
      },
    },

    "& .link-head": {
      alignItems: "center",
      display: "flex",
      columnGap: "14px",
    },
  },

  "select-platform": {
    marginTop: "35px",

    "& h3": {
      color: "#434A54",
      fontSize: "20px",
      fontWeight: 500,
      lineHeight: "23px",
      marginBottom: "14px",
    },

    "& > hr": {
      border: "none",
      borderTop: "1px solid #24272C",
      marginBottom: "18px",
      opacity: 0.12,
    },

    "& ul": {
      listStyle: "none",
      paddingLeft: "16px",

      "& li": {
        marginBottom: "20px",

        "& p": {
          color: "#434A54",
          fontSize: "16px",
          fontWeight: 500,
          lineHeight: "19px",
          marginBottom: "10px",
        },

        "& .MuiButton-root": {
          color: "#ffffff",
          height: "48px",
          padding: "0 10px",
          textTransform: "initial",

          "& .MuiButton-label": {
            height: "100%",
            columnGap: "10px",
          },

          "&.btn-facebook": {
            backgroundColor: "#4267B2",
          },

          "&.btn-twitter": {
            backgroundColor: "#1DA1F2",
          },

          "&.btn-linkedin": {
            backgroundColor: "#0077b5",
          },
        },
      },
    },
  },

  platform: {
    "&.facebook, &.instagram,& .linkedin": {
      "& .MuiList-root": {
        "& .MuiListItem-root": {
          "& .MuiListItemButton-root": {
            "& .MuiListItemAvatar-root": {
              display: "flex",
            },
            "& .MuiListItemText-primary": {
              color: "#24272C",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "19px",
            },

            "& .MuiListItemText-secondary": {
              color: "#F7B500",
              fontSize: "12px",
              fontWeight: 400,
              lineHeight: "14px",
            },

            "& .MuiListItemIcon-root": {
              minWidth: "unset",
            },
          },
        },
      },
    },

    "&.twitter": {
      "& .MuiList-root": {
        backgroundColor: "#F6F9FB",

        "& > .MuiListItemText-root": {
          color: "#24272C",
          fontSize: "14px",
          fontWeight: 400,
          lineHeight: "16px",
          paddingLeft: "48px",
          textDecoration: "underline",
        },

        "& .MuiListItem-root": {
          "& .MuiListItemButton-root": {
            padding: "0 26px",
            "& .MuiListItemText-primary": {
              color: "#24272C",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "19px",
            },

            "& .MuiListItemIcon-root": {
              minWidth: "unset",
            },
          },
        },
      },
    },

    "& .platform-head": {
      "& > p": {
        color: "#24272C",
        fontSize: "14px",
        fontWeight: 400,
        lineHeight: "20px",
        marginBottom: "6px",
        opacity: 0.7,
      },

      "& > a": {
        color: "#656F7A",
        cursor: "pointer",
        fontSize: "14px",
        fontWeight: 500,
        lineHeight: "16px",

        "&:hover": {
          textDecoration: "underline",
        },
      },
    },

    "& > .MuiButton-root": {
      color: "#ffffff",
      height: "48px",
      padding: "0 10px",
      textTransform: "initial",
      margin: "32px 0",

      "& .MuiButton-label": {
        height: "100%",
        columnGap: "10px",
      },

      "&.btn-facebook": {
        backgroundColor: "#4267B2",
      },

      "&.btn-twitter": {
        backgroundColor: "#1DA1F2",
      },

      "&.btn-linkedin": {
        backgroundColor: "#0077b5",
      },
    },

    "&.linkedin": {
      "& .actions": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",

        "& .btn-next": {
          alignSelf: "flex-start",
          background: "#12B0EE",
          borderRadius: "6px",
          color: "#ffffff",
          fontSize: "16px",
          fontWeight: 700,
          height: "48px",
          lineHeight: "19px",

          "& .MuiButton-label": {
            columnGap: "6px",
          },
        },
      },
    },

    "& .actions": {
      textAlign: "center",
      display: "table",
      margin: "0 auto",

      "& .skip": {
        alignItems: "center",
        color: "#24272C",
        cursor: "pointer",
        display: "inline-flex",
        fontSize: "13px",
        fontWeight: 400,
        lineHeight: "15px",
        opacity: 0.7,
        textDecoration: "underline",

        "&:disabled": {
          cursor: "not-allowed",
        },

        "& .label": {
          marginRight: "6px",
        },
      },
    },
  },

  "final-step": {
    marginTop: "36px",

    "& .MuiAccordion-root": {
      boxShadow: "none",

      "&::before": {
        display: "none",
      },

      "&.Mui-expanded": {
        margin: 0,

        "& .MuiAccordionSummary-root": {
          minHeight: "unset",

          "& .MuiAccordionSummary-content": {
            margin: "14px 0",
          },
        },
      },

      "& .MuiAccordionSummary-root": {
        borderBottom: "1px solid #24272c1f",

        "& .MuiAccordionSummary-content": {
          margin: "14px 0",

          "& .MuiTypography-root": {
            color: "#434A54",
            fontSize: "20px",
            fontWeight: 500,
            lineHeight: "23px",
          },
        },
      },

      "& .MuiCollapse-root": {
        "& .MuiListItemAvatar-root": {
          display: "flex",
          justifyContent: "end",
          columnGap: "10px",
          marginTop: "4px",
        },

        "& .MuiListItem-root": {
          alignItems: "flex-start",
          marginBottom: "30px",

          "& .MuiListItemText-root": {
            marginBottom: "12px",

            "& .MuiListItemText-primary": {
              color: "#434A54",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "19px",
            },
          },

          "& .linked-account": {
            alignItems: "center",
            display: "flex",
            justifyContent: "flex-start",
            columnGap: "12px",
            marginBottom: "8px",
            width: "100%",

            "& > img": {
              display: "flex",
              backgroundColor: "#FFFFFF",
              border: "1px solid rgba(36, 39, 44, 0.1)",
              borderRadius: "8px",
              overflow: "hidden",
            },

            "& .account-name": {
              color: "#24272C",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "19px",
            },
          },

          "& .actions": {
            display: "flex",
            justifyContent: "space-between",
            columnGap: "32px",

            "& .MuiLink-root": {
              color: "#656F7A",
              cursor: "pointer",
              fontSize: "12px",
              fontWeight: 500,
              lineHeight: "14px",
              textDecorationColor: "#656F7A",
            },
          },
        },
      },
    },

    "& .btn-finish": {
      alignSelf: "flex-start",
      background: "#12B0EE",
      borderRadius: "6px",
      color: "#ffffff",
      fontSize: "16px",
      fontWeight: 700,
      height: "48px",
      lineHeight: "19px",

      "&:hover": {
        background: "#12B0EE",
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {
    "entry-header": {
      paddingTop: 0,

      "& .link-block": {
        position: "relative",

        "&::before,&::after": {
          content: '""',
          display: "block",
          position: "absolute",
          borderBottom: "3px solid #12B0EE",
          width: "100%",
          top: "50%",
          transform: "translateY(-50%)",
        },

        "&::before": {
          right: "100%",
        },

        "&::after": {
          left: "100%",
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));
