/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Fragment, useState, useEffect } from "react";
import {
  List,
  ListItemAvatar,
  ListItemIcon,
  ListItemText,
  Divider,
} from "@material-ui/core";
import { ListItem, ListItemButton, Skeleton } from "@mui/material";
import { CircleOutlined, CheckCircleOutlined } from "@mui/icons-material";

import { UIButton } from "../../exports";

// import Page1Img from "../assets/page-1.svg";

// request helpers imports
import {
  fetchIGAccountData,
  saveIGAccountData,
  fetchSelectedFBPage,
} from "../../../api/SocialMediaSetup";

// toast notifications
import { useToastMessage } from "../../../contexts/ToastMessageContext";

const SelectInstagramAccount = ({
  step,
  className,
  setHead,
  setStep,
  continueTo,
  skipPlatFormTo,
  goToFinalStep,
  toLinkOnlyAccount,
}) => {
  const [pages, setPages] = useState([
      // {
      //   image: Page1Img,
      //   name: "Honda bikes",
      //   selected: false,
      // },
    ]),
    // selectPage = (selectedIndex, igAccID) => {
    //   setPages((prev) =>
    //     prev.map((page, index) =>
    //       selectedIndex === index
    //         ? { ...page, selected: !page.selected }
    //         : { ...page }
    //     )
    //   );

    //   setSelectedPageID(igAccID);
    // },
    selectPage = (selectedIndex, igAcc) => {
      setPages((prev) =>
        prev.map((page, index) => {
          if (selectedIndex === index) {
            if (page.selected) {
              setSelectedPage(null);
            } else {
              setBTNDisable(false);
              setSelectedPage(igAcc);
            }
            return { ...page, selected: !page.selected };
          } else {
            return { ...page };
          }
        })
      );
    },
    [selectedPage, setSelectedPage] = useState(null),
    [fetchIGInfo, setFetchIGInfo] = useState(false),
    [noFBPageFound, setNoFBPageFound] = useState(false),
    [noLinkedIGAcc, setNoLinkedIGAcc] = useState(false),
    [BTNDisable, setBTNDisable] = useState(false),
    [connectIGURL, setConnectIGURL] = useState(null);

  // contexts
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  // component effects
  useEffect(() => {
    loadFBSelectedPage();
  }, []);
  const loadFBSelectedPage = async () => {
    try {
      const res = await fetchSelectedFBPage();
      if (res.success) {
        setFetchIGInfo(true);
      } else {
        setNoFBPageFound(true);
      }
    } catch (err) {
      setErrorMessage(err.message);
    }

    /* .then(({ selectedFBPage }) => {
        if (selectedFBPage) {
          setFetchIGInfo(true);
        } else {
          setNoFBPageFound(true);
        }
      })
      .catch(error => {});  */
  };

  useEffect(() => {
    if (fetchIGInfo) {
      fetchIGAccountInfo();
    }
  }, [fetchIGInfo]);

  const fetchIGAccountInfo = async () => {
    const res = await fetchIGAccountData();
    if (res.success) {
      const igPageData = {
        igAccID: res.payload.account_id,
        name: res.payload.name,
        username: res.payload.username,
        image: res.payload.profile_picture,
        selected: false,
      };

      setPages([igPageData]);
    }
    /* .then(data => {
      console.log("fetch ig account data: ", data);


    })
    .catch(error => {
      setPages([]);

      const { noIGAccConnError, igConnectURL } = error;

      if (noIGAccConnError) {
        setNoLinkedIGAcc(true);
        setBTNDisable(true);
        setConnectIGURL(igConnectURL);
        // window.open(igConnectURL, "_blank");
      }
    }); */
  };

  // Event handlers
  const handleConnectIGAccount = async () => {
    if (!selectedPage) {
      return setErrorMessage("Please select an instagram account");
    }

    const igReqData = {
      account_id: selectedPage.igAccID,
      profile_picture: selectedPage.image,
      name: selectedPage.name,
      username: selectedPage.username,
    };

    try {
      const res = await saveIGAccountData(igReqData);
      if (res.success) {
        if (toLinkOnlyAccount) {
          goToFinalStep();
        } else {
          setHead("Login with Twitter");
          continueTo("twitter");
          setSuccessMessage("Instagram setup complete!");
        }
      }
    } catch (error) {
      const { message, error: err } = error;

      if (message) {
        setErrorMessage(message);
      } else if (err) {
        setErrorMessage(err);
      } else {
        setErrorMessage(error);
      }
    }
  };

  const ListItemImage = ({ src, alt }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => {
      setLoading(false);
    };

    return (
      <ListItemAvatar>
        {loading && <Skeleton variant="rectangular" width={50} height={50} />}
        {src && (
          <img
            src={src}
            alt={alt}
            onLoad={onImageLoad}
            width={loading ? 0 : 50}
            height={loading ? 0 : 50}
            style={{ maxWidth: "100%" }}
          />
        )}
      </ListItemAvatar>
    );
  };

  return (
    <div className={className}>
      <List>
        {pages.length > 0 &&
          pages.map(({ image, name, username, selected, igAccID }, index) => (
            <Fragment>
              <ListItem key={index} selected={selected} sx={{ p: 0 }}>
                <ListItemButton
                  onClick={() =>
                    selectPage(index, {
                      image,
                      name,
                      username,
                      selected,
                      igAccID,
                    })
                  }
                >
                  <ListItemImage src={image} alt={username} />
                  <ListItemText primary={username} />
                  <ListItemIcon>
                    {selected ? (
                      <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                    ) : (
                      <CircleOutlined sx={{ color: "#24272C80" }} />
                    )}
                  </ListItemIcon>
                </ListItemButton>
              </ListItem>
              {index !== pages.length - 1 && <Divider />}
            </Fragment>
          ))}
      </List>

      {/* NO FB page is linked */}
      {noFBPageFound && (
        <>
          <p>
            Sorry, we couldn't find any associated business Instagram account
            since you've not linked any facebook page. Linking a Facebook page
            is a prerequisite to link an Instagram account.
          </p>
        </>
      )}

      {noLinkedIGAcc && connectIGURL && (
        <>
          <p>
            Sorry, we couldn't find an associated Instagram account with your
            facebook page.{" "}
            <a href={connectIGURL}>
              Click here to link your Instagram account to facebook page.
            </a>
          </p>
        </>
      )}
      <div className="actions">
        <UIButton
          variant="contained"
          className="btn-next"
          style={{
            marginTop: "40px",
            marginBottom: "30px",
            marginLeft: 0,
          }}
          // disabled={!setSelectedPageID}
          disabled={BTNDisable}
          onClick={handleConnectIGAccount}
        >
          {"Link & Continue to Twitter"}
        </UIButton>
        <a className="skip" onClick={() => skipPlatFormTo("twitter")}>
          <span className="label">{"Skip Instagram"}</span>
          <svg
            width="6"
            height="10"
            viewBox="0 0 6 10"
            fill="none"
            xmlns="//www.w3.org/2000/svg"
          >
            <path
              opacity="0.7"
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.483211 0.526985C0.221976 0.811968 0.241229 1.25477 0.526212 1.516L3.96329 4.66666L0.526212 7.81732C0.241229 8.07855 0.221976 8.52135 0.483211 8.80633C0.744446 9.09132 1.18724 9.11057 1.47223 8.84933L5.47223 5.18267C5.61687 5.05008 5.69922 4.86287 5.69922 4.66666C5.69922 4.47044 5.61687 4.28324 5.47223 4.15065L1.47223 0.483984C1.18724 0.222749 0.744446 0.242001 0.483211 0.526985Z"
              fill="#24272C"
            />
          </svg>
        </a>
      </div>
    </div>
  );
};

export default SelectInstagramAccount;
