import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  popup: {
    "& .MuiDialog-paper": {
      backgroundColor: "#FFFFFF",
      border: "1px solid rgba(0, 0, 0, 0.1)",
      boxShadow: "0px 5px 40px rgba(18, 176, 238, 0.1)",
      borderRadius: 0,

      "& .MuiDialogTitle-root": {
        padding: "20px 40px",

        "& big": {
          color: "#2E3239",
          display: "block",
          fontSize: "24px",
          fontWeight: 700,
          lineHeight: "28px",
          textTransform: "capitalize",
        },

        "& small": {
          color: "#2E3239",
          fontWeight: 700,
          fontSize: "18px",
          lineHeight: "21px",
        },
      },

      "& .MuiDialogContent-root": {
        color: "#24272C",
        padding: "10px 40px 40px",
        maxHeight: "55vh",

        [`${theme.breakpoints.down("md")} and (orientation: landscape)`]: {
          maxHeight: "33vh",
        },

        "& .permission-list": {
          listStyle: "none",

          "& .permission-item": {
            marginBottom: "16px",

            "& .permission-name": {
              color: "#000000",
              fontSize: "14px",
              fontWeight: 700,
              lineHeight: "16px",
            },

            "& .permission-desc": {
              color: "#000000",
              fontSize: "14px",
              fontWeight: 400,
              lineHeight: "16px",
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
