import React, { useState, useEffect, useRef } from "react";

import { TextField } from "@material-ui/core";
import Timeline from "@material-ui/lab/Timeline";
import {
  TimelineItem,
  TimelineSeparator,
  TimelineConnector,
  TimelineContent,
} from "@material-ui/lab";
import { CircleOutlined, CheckCircleOutlined } from "@mui/icons-material";
import PhoneInput from "react-phone-input-2";

import WarningIcon from "../../assets/icons/warning.svg";
import VideoPlaceholder from "../../assets/icons/video-placeholder.svg";
import { UIButton } from "../exports";

import { useToastMessage } from "../../contexts";

import { saveWaAppDetail, getBusinessDetail } from "../../api/Business";

import "react-phone-input-2/lib/material.css";

const NewSetupWhatsappChatbot = () => {
  const connectorCompleted = "#12B0EE",
    primary = "#12B0EE",
    secondary = "#CED5E1",
    active = "#E50000",
    allComplete = "#00CD39",
    inActiveLine = "rgba(36, 39, 44,0.1)",
    toastMessage = useToastMessage(),
    [fb_verify_error, setFb_verify_error] = useState(null),
    [name_error, setName_error] = useState(null),
    [step, setStep] = useState({
      first: true,
      second: false,
      third: false,
      fourth: false,
      final: false,
    }),
    [current, setCurrent] = useState(2),
    [waData, setWadata] = useState({
      access_token: "",
      phone_number_id: "",
      waba_id: "",
      phone_number: "",
      app_id: "",
      version: "v13.0",
    }),
    { access_token, phone_number_id, waba_id, phone_number, app_id } = waData,
    handleChange = (field) => {
      setStep((prev) => {
        return { ...prev, [field]: true };
      });
    },
    submitScreenShot = async () => {
      setCurrent(3);
      setStep({ ...step, second: true });
    },
    preload = useRef(async () => {
      //const res = await getBusinessDetail();
    }),
    submitWaDetail = async () => {
      const data = {
        whatsapp: waData,
        type: "WHATSAPP",
        product: "engage",
      };
      const res = await saveWaAppDetail(data);
      if (!res.success) {
        return toastMessage.setErrorMessage(
          res.messages ? res.messages.errors[0].message : res.message
        );
      } else {
        setCurrent(4);
        setStep({ ...step, third: true });
      }
    },
    submitChatbotfinal = () => {
      setCurrent(5);
      setStep({ ...step, fourth: true });
    };

  useEffect(() => {
    preload.current();
  }, []);

  false && console.log(submitScreenShot);

  return (
    <div className={"chatbotOuterWrapper"}>
      <Timeline>
        <TimelineItem style={{ minHeight: "56px" }}>
          {step.first ? (
            <TimelineSeparator>
              <CheckCircleOutlined
                sx={{
                  color: primary,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
              <TimelineConnector
                style={{ backgroundColor: connectorCompleted, width: 3 }}
              />
            </TimelineSeparator>
          ) : (
            <TimelineSeparator>
              <CircleOutlined
                sx={{
                  color: secondary,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
              <TimelineConnector />
            </TimelineSeparator>
          )}
          <TimelineContent style={{ padding: "0 10px" }}>
            <span className={"timelineContentText"}>
              {"Setup easy social business account"}
            </span>
          </TimelineContent>
        </TimelineItem>
        <TimelineItem style={{ minHeight: "56px" }}>
          {step.second ? (
            <TimelineSeparator>
              <CheckCircleOutlined
                sx={{
                  color: primary,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
              <TimelineConnector
                style={{ backgroundColor: connectorCompleted, width: 3 }}
              />
            </TimelineSeparator>
          ) : (
            <TimelineSeparator>
              <CircleOutlined
                sx={{
                  color: current === 2 ? active : secondary,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
              <TimelineConnector
                style={{
                  backgroundColor: inActiveLine,
                  width: 3,
                }}
              />
            </TimelineSeparator>
          )}
          <TimelineContent style={{ padding: "0 10px" }}>
            <span className={"timelineContentText"}>
              {"Verify facebook business account"}
            </span>
            {current === 2 && (
              <div className={"screenShotblock"}>
                <p style={{ marginBottom: "20px" }}>
                  {
                    "Go through the video instructions below and verify your Facebook Business Manager account and upload a screenshot showing that your business is verified."
                  }
                </p>
                <div className="video">
                  <img
                    src={VideoPlaceholder}
                    alt="VideoPlaceholder"
                    className="video-img"
                  />
                </div>

                {fb_verify_error && (
                  <div className={"errorblock"}>
                    <div className="error-head">
                      <img className="icon" src={WarningIcon} alt="warning" />
                      <h4 className="heading">{"ERROR"}</h4>
                    </div>
                    <p>{fb_verify_error}</p>
                  </div>
                )}
                <UIButton
                  variant="contained"
                  className="btn-submit"
                  onClick={submitScreenShot}
                  style={{
                    marginTop: "22px",
                    marginBottom: "48px",
                    marginLeft: 0,
                    float: "right",
                  }}
                >
                  {"Next"}
                </UIButton>
              </div>
            )}
          </TimelineContent>
        </TimelineItem>
        <TimelineItem style={{ minHeight: "56px" }}>
          {step.third ? (
            <TimelineSeparator>
              <CheckCircleOutlined
                sx={{
                  color: primary,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
              <TimelineConnector
                style={{ backgroundColor: connectorCompleted, width: 3 }}
              />
            </TimelineSeparator>
          ) : (
            <TimelineSeparator>
              <CircleOutlined
                sx={{
                  color: current === 3 ? active : secondary,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
              <TimelineConnector
                style={{
                  backgroundColor: inActiveLine,
                  width: 3,
                }}
              />
            </TimelineSeparator>
          )}
          <TimelineContent style={{ padding: "0 10px" }}>
            <span className={"timelineContentText"}>
              {"Get approval on WhatsApp business name & phone number"}
            </span>
            {current === 3 && (
              <div className={"screenShotblock"}>
                <p style={{ marginBottom: "10px" }}>
                  {
                    "Select a unique phone number that is not currently linked to any WhatsApp number or  logged on to any device. Incase the number is in use on whatsapp, login with the number on WhatsApp on any device. And delete the WhatsApp account associated with the number."
                  }
                </p>
                <PhoneInput
                  value={phone_number}
                  country={"in"}
                  onChange={(phone) =>
                    setWadata({ ...waData, phone_number: `+${phone}` })
                  }
                />

                <TextField
                  className="form-input"
                  label="Access Token"
                  variant="filled"
                  fullWidth
                  placeholder="Enter Access Token"
                  name="access_token"
                  value={access_token}
                  onChange={(e) =>
                    setWadata({ ...waData, access_token: e.target.value })
                  }
                />

                <TextField
                  className="form-input"
                  label="Phone Number Id"
                  variant="filled"
                  fullWidth
                  placeholder="Phone Number Id"
                  name="phone_number_id"
                  value={phone_number_id}
                  onChange={(e) =>
                    setWadata({ ...waData, phone_number_id: e.target.value })
                  }
                />

                <TextField
                  className="form-input"
                  label="WABA Id "
                  variant="filled"
                  fullWidth
                  placeholder="WABA Id"
                  name="waba_id"
                  value={waba_id}
                  onChange={(e) =>
                    setWadata({ ...waData, waba_id: e.target.value })
                  }
                />

                <TextField
                  className="form-input"
                  label="App id"
                  variant="filled"
                  fullWidth
                  placeholder="App Id"
                  name="app_id"
                  value={app_id}
                  onChange={(e) =>
                    setWadata({ ...waData, app_id: e.target.value })
                  }
                />
                {name_error && (
                  <div className={"errorblock"}>
                    <div className="error-head">
                      <img className="icon" src={WarningIcon} alt="warning" />
                      <h4 className="heading">{"ERROR"}</h4>
                    </div>
                    <p>{name_error}</p>
                  </div>
                )}
                <UIButton
                  variant="contained"
                  className="btn-submit"
                  onClick={submitWaDetail}
                  style={{
                    marginTop: "22px",
                    marginBottom: "48px",
                    marginLeft: 0,
                    float: "right",
                  }}
                >
                  {"Submit"}
                </UIButton>
              </div>
            )}
          </TimelineContent>
        </TimelineItem>
        <TimelineItem style={{ minHeight: "56px" }}>
          {step.fourth ? (
            <TimelineSeparator>
              <CheckCircleOutlined
                sx={{
                  color: primary,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
            </TimelineSeparator>
          ) : step.final ? (
            <TimelineSeparator>
              <CheckCircleOutlined
                sx={{
                  color: allComplete,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
            </TimelineSeparator>
          ) : (
            <TimelineSeparator>
              <CircleOutlined
                sx={{
                  color: current === 4 ? active : secondary,
                  fontSize: 23,
                  transform: "scale(1.2)",
                }}
              />
            </TimelineSeparator>
          )}
          <TimelineContent style={{ padding: "0 10px" }}>
            <span className={"timelineContentText"}>
              {"Design conversational chatflow"}
            </span>
            {current === 4 && (
              <div className={"screenShotblock"}>
                <p style={{ marginBottom: "14px" }}>
                  {
                    "Please wait while we get your number activated on whatsapp. Usually this process takes 24-48 hours."
                  }
                </p>
                <p>{"We will inform you when your number is activated"}</p>
                <p
                  style={{
                    marginTop: "190px",
                    opacity: "100%",
                    marginBottom: "15px",
                  }}
                >
                  {
                    "Setup required to manage & schedule posts on popular social media networks from one place."
                  }
                </p>
                <UIButton
                  variant="contained"
                  className="btn-submit"
                  onClick={submitChatbotfinal}
                  style={{
                    marginTop: "22px",
                    marginBottom: "48px",
                    marginLeft: 0,
                  }}
                >
                  {"Setup Social Scheduling"}
                </UIButton>
              </div>
            )}
            {current === 5 && (
              <div className={"screenShotblock"}>
                <div className={"chatBotDiv"}>
                  <p>
                    {"Your WhatsApp number +91343434334 is now active. "}
                    <a className={"try"}>Try now</a>
                  </p>
                  <p style={{ marginTop: "20px" }}>
                    {
                      "You design a conversation without any coding knowledge using our chat flow tool. Click on the button below and customize your conversation."
                    }
                  </p>
                </div>
                <UIButton
                  variant="contained"
                  className="btn-submit"
                  onClick={() => handleChange("final")}
                  style={{
                    marginTop: "22px",
                    marginBottom: "48px",
                    marginLeft: 0,
                  }}
                >
                  {"Design Chat Flow"}
                </UIButton>
              </div>
            )}
          </TimelineContent>
        </TimelineItem>
      </Timeline>
    </div>
  );
};

export default NewSetupWhatsappChatbot;
