import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import {
  Button,
  Link,
  List,
  ListItemAvatar,
  ListItem,
  ListItemText,
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Typography,
} from "@mui/material";

import { ExpandMore } from "@mui/icons-material";

import FacebookIcon from "../assets/facebook.svg";
import InstagramIcon from "../assets/instagram.svg";
import TwitterIcon from "../assets/twitter.svg";
import LinkedInIcon from "../assets/linkedin.svg";

import PermissionsPopUp from "../PermissionsPopUp";

// api imports
import {
  fetchSetupInfo,
  unlinkSocialMediaAccount,
  fetchBusinessLogo,
  // fetchPlatformPermissions,
} from "../../../api/SocialMediaSetup";

// toast notifications
import { useToastMessage } from "../../../contexts/ToastMessageContext";

// utils
import { formatFBIGAccountsArray } from "./utils";

const FinalStep = ({
  className,
  setSetupPlatform,
  setStep,
  setFinished,
  setToLinkFBIG,
  setTWUser,
  setToLinkOnlyAccount,
  setLIUser,
}) => {
  const history = useHistory();
  const [expanded, setExpanded] = useState("linked"),
    [showPermission, setShowPermission] = useState(false),
    [permissionState, setPermissionState] = useState("granted"),
    [platform, setPlatform] = useState(null),
    [connectedAccounts, setConnectedAccounts] = useState([]),
    [unconnectedAccounts, setUnconnectedAccounts] = useState([]),
    [logoURL, setLogoURL] = useState(null),
    openPermissionModal = (platform) => {
      setShowPermission(true);
      setPlatform(platform);
    },
    closePermissionModal = (event, reason) => {
      setShowPermission(false);
      setPlatform(null);
    },
    handleChange = (panel) => (_, isExpanded) => {
      setExpanded(isExpanded ? panel : false);
    };

  // contexts
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  // component effects
  useEffect(() => {
    fetchSetupInfo()
      .then((res) => {
        setConnectedAccounts(res.payload.connectedAccounts);

        let withChangedFBAccounts = formatFBIGAccountsArray(
          res.payload.unconnectedAccounts
        );

        setUnconnectedAccounts(withChangedFBAccounts);
      })
      .catch(() => {
        // console.log("Fetch setup error: ", error);
        setErrorMessage("Failed to fetch setup info!");
      });

    fetchBusinessLogo()
      .then(({ logoURL }) => {
        // console.log("business logo url: ", logoURL);
        setLogoURL(logoURL);
      })
      .catch(({ error }) => {
        console.log("account info error: ", error);
        // setErrorMessage(error);
      });
  }, []);

  const handleAccountUnlink = async (platform) => {
    console.log("platform: ", platform);

    try {
      const { success } = await unlinkSocialMediaAccount({
        platform,
        product: "curate",
      });

      if (success) {
        setSuccessMessage(
          `${`${platform.charAt(0)}${platform.slice(1)}`} unlinked!`
        );

        let newConnectedAccounts;

        if (platform === "facebook" || platform === "instagram") {
          newConnectedAccounts = connectedAccounts.filter(
            (acc) => acc.platform !== platform
          );

          setConnectedAccounts(newConnectedAccounts);
          // setUnconnectedAccounts(
          //   formatFBIGAccountsArray([
          //     ...unconnectedAccounts,
          //     "facebook and instagram",
          //   ])
          // );

          if (
            // !unconnectedAccounts.includes("facebook") ||
            // !unconnectedAccounts.includes("instagram") ||
            !unconnectedAccounts.includes("facebook and instagram")
          ) {
            setUnconnectedAccounts(
              formatFBIGAccountsArray([...unconnectedAccounts, platform])
            );
            // setUnconnectedAccounts(
            //   formatFBIGAccountsArray([...unconnectedAccounts])
            // );
          }
          // else {
          //   setUnconnectedAccounts(
          //     formatFBIGAccountsArray([...unconnectedAccounts, platform])
          //   );
          // }
        } else {
          // shift the account from connected to unconnected array
          newConnectedAccounts = connectedAccounts.filter(
            (acc) => acc.platform !== platform
          );
          setConnectedAccounts(newConnectedAccounts);
          // setUnconnectedAccounts([...unconnectedAccounts, platform]);
          setUnconnectedAccounts(
            formatFBIGAccountsArray([...unconnectedAccounts, platform])
          );
        }
      }
    } catch ({ error }) {
      // this.context.setErrorMessage(error);
      // console.log("Unlink account error: ", error);
      setErrorMessage(error);
    }
  };

  const handleAccountLink = (acc) => {
    // Change the step, platform values and show the platform connection jsx

    // if (acc === "facebook and instagram") {
    //   setSetupPlatform(null);
    //   setStep(0);
    // } else if (acc === "twitter") {
    //   setSetupPlatform(acc);
    //   setStep(0);
    // } else if (acc === "linkedin") {
    //   setSetupPlatform(null);
    //   setStep(0);
    // }

    setFinished(false);

    if (
      acc === "facebook and instagram" ||
      acc === "facebook" ||
      acc === "instagram"
    ) {
      setSetupPlatform(null);
      setStep(0);
      setToLinkFBIG(true);
    } else {
      setSetupPlatform(acc);
    }
  };

  const goToCalendar = () => {
    history.push({
      pathname: `/curate/calendar`,
    });
  };

  return (
    <div className={className}>
      {connectedAccounts.length > 0 && (
        <Accordion
          expanded={expanded === "linked"}
          onChange={handleChange("linked")}
        >
          <AccordionSummary
            expandIcon={<ExpandMore sx={{ color: "#24272C" }} />}
          >
            <Typography>{"Linked Accounts"}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <List>
              {connectedAccounts.map((acc) => {
                const { platform, accountName, accountProfileIMG } = acc;

                if (platform === "facebook and instagram") {
                  return (
                    <>
                      <ListItem sx={{ p: 0, flexWrap: "wrap" }}>
                        <ListItemText primary={"Facebook and Instagram"} />
                        <ListItemAvatar>
                          <img
                            src={FacebookIcon}
                            alt="Facebook"
                            height={18}
                            width={18}
                          />
                          <img
                            src={InstagramIcon}
                            alt="Instagram"
                            height={18}
                            width={18}
                          />
                        </ListItemAvatar>
                        <div className="actions">
                          {/* <Link
                            underline="always"
                            onClick={() => handleAccountUnlink(acc)}
                          >
                            {"Unlink Account"}
                          </Link> */}
                          <Link
                            underline="always"
                            onClick={() => {
                              setPermissionState("granted");
                              openPermissionModal("facebook");
                            }}
                          >
                            {"View Permissions"}
                          </Link>
                        </div>
                      </ListItem>
                    </>
                  );
                }

                let icon = null;
                let account = null;

                switch (platform) {
                  case "twitter":
                    icon = TwitterIcon;
                    account = "Twitter";
                    break;

                  case "linkedin":
                    icon = LinkedInIcon;
                    account = "LinkedIn";
                    break;

                  case "facebook":
                    icon = FacebookIcon;
                    account = "Facebook";
                    break;

                  case "instagram":
                    icon = InstagramIcon;
                    account = "Instagram";
                    break;

                  default:
                    break;
                }

                return (
                  <>
                    <ListItem sx={{ p: 0, flexWrap: "wrap" }}>
                      {/* <ListItemText primary={"LinkedIn"} /> */}
                      <ListItemText primary={account} />
                      <ListItemAvatar>
                        <img
                          // src={LinkedInIcon}
                          src={icon}
                          alt={account}
                          height={18}
                          width={18}
                        />
                      </ListItemAvatar>

                      <div className="linked-account">
                        <img
                          src={accountProfileIMG}
                          // src={logoURL}
                          alt={account}
                          height={40}
                          width={40}
                        />

                        <span className="account-name">{accountName}</span>
                      </div>
                      <div className="actions">
                        <Link
                          underline="always"
                          onClick={() => handleAccountUnlink(acc.platform)}
                        >
                          {"Unlink Account"}
                        </Link>
                        <Link
                          underline="always"
                          onClick={() => {
                            setPermissionState("granted");
                            openPermissionModal(acc.platform);
                          }}
                        >
                          {"View Permissions"}
                        </Link>
                      </div>
                    </ListItem>
                  </>
                );
              })}
              {/* <ListItem sx={{ p: 0, flexWrap: "wrap" }}>
              <ListItemText primary={"Facebook and Instagram"} />
              <ListItemAvatar>
                <img src={FacebookIcon} alt="Facebook" height={18} width={18} />
                <img
                  src={InstagramIcon}
                  alt="Instagram"
                  height={18}
                  width={18}
                />
              </ListItemAvatar>
              <div className="actions">
                <Link underline="always">{"Unlink Account"}</Link>
                <Link
                  underline="always"
                  onClick={() => {
                    setPermissionState("granted");
                    openPermissionModal("facebook");
                  }}
                >
                  {"View Permissions"}
                </Link>
              </div>
            </ListItem> */}
              {/* <ListItem sx={{ p: 0, flexWrap: "wrap" }}>
              <ListItemText primary={"LinkedIn"} />
              <ListItemAvatar>
                <img src={LinkedInIcon} alt="LinkedIn" height={18} width={18} />
              </ListItemAvatar>
              <div className="actions">
                <Link underline="always">{"Unlink Account"}</Link>
                <Link
                  underline="always"
                  onClick={() => {
                    setPermissionState("granted");
                    openPermissionModal("linkedin");
                  }}
                >
                  {"View Permissions"}
                </Link>
              </div>
            </ListItem> */}
            </List>
          </AccordionDetails>
        </Accordion>
      )}

      {unconnectedAccounts.length > 0 && (
        <Accordion
          expanded={expanded === "more"}
          onChange={handleChange("more")}
        >
          <AccordionSummary
            expandIcon={<ExpandMore sx={{ color: "#24272C" }} />}
          >
            <Typography>{"Link More Accounts"}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <List>
              {unconnectedAccounts.map((acc) => {
                if (
                  acc === "facebook and instagram" ||
                  acc === "facebook" ||
                  acc === "instagram"
                ) {
                  return (
                    <>
                      <ListItem sx={{ p: 0, flexWrap: "wrap" }}>
                        <ListItemText primary={"Facebook and Instagram"} />
                        <ListItemAvatar>
                          <img
                            src={FacebookIcon}
                            alt="Facebook"
                            height={18}
                            width={18}
                          />
                          <img
                            src={InstagramIcon}
                            alt={"Instagram"}
                            height={18}
                            width={18}
                          />
                        </ListItemAvatar>
                        {/* <div className="actions">
                          <Link
                            underline="always"
                            onClick={handleAccountUnlink}
                          >
                            {"Unlink Account"}
                          </Link>
                          <Link
                            underline="always"
                            onClick={() => {
                              setPermissionState("granted");
                              openPermissionModal("facebook");
                            }}
                          >
                            {"View Permissions"}
                          </Link>
                        </div> */}
                        <div className="actions">
                          <Link
                            underline="always"
                            onClick={() => handleAccountLink(acc)}
                          >
                            {"Link Account"}
                          </Link>
                          <Link
                            underline="always"
                            onClick={() => {
                              setPermissionState("required");
                              openPermissionModal(acc);
                            }}
                          >
                            {"Permissions Required"}
                          </Link>
                        </div>
                      </ListItem>
                    </>
                  );
                }

                let icon = null;
                let account = null;

                switch (acc) {
                  case "twitter":
                    icon = TwitterIcon;
                    account = "Twitter";
                    break;

                  case "linkedin":
                    icon = LinkedInIcon;
                    account = "LinkedIn";
                    break;

                  // case "facebook":
                  //   icon = FacebookIcon;
                  //   account = "Facebook";
                  //   break;

                  // case "instagram":
                  //   icon = InstagramIcon;
                  //   account = "Instagram";
                  //   break;

                  default:
                    break;
                }

                return (
                  <>
                    <ListItem sx={{ p: 0, flexWrap: "wrap" }}>
                      {/* <ListItemText primary={"Twitter"} /> */}
                      <ListItemText primary={account} />
                      <ListItemAvatar>
                        <img src={icon} alt={account} height={18} width={18} />
                      </ListItemAvatar>
                      <div className="actions">
                        <Link
                          underline="always"
                          // onClick={() => handleAccountLink(acc)}
                          onClick={() => handleAccountLink(platform)}
                        >
                          {"Link Account"}
                        </Link>
                        <Link
                          underline="always"
                          onClick={() => {
                            setPermissionState("required");
                            // openPermissionModal(acc);
                            openPermissionModal(platform);
                          }}
                        >
                          {"Permissions Required"}
                        </Link>
                      </div>
                    </ListItem>
                  </>
                );
              })}
            </List>
          </AccordionDetails>
        </Accordion>
      )}

      {connectedAccounts.length > 0 && (
        <Button
          variant="contained"
          className="btn-finish"
          style={{
            marginTop: "40px",
            marginBottom: "30px",
            marginLeft: 0,
          }}
          onClick={goToCalendar}
        >
          {"Start Scheduling Posts"}
        </Button>
      )}

      <PermissionsPopUp
        open={showPermission}
        platform={platform}
        permissionState={permissionState}
        doClose={closePermissionModal}
      />
    </div>
  );
};

export default FinalStep;
