/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Fragment, useState, useEffect } from "react";
import {
  Button,
  List,
  ListItemAvatar,
  ListItemIcon,
  ListItemText,
  Divider,
} from "@material-ui/core";
import { ListItem, ListItemButton, Skeleton } from "@mui/material";
import {
  Twitter,
  CircleOutlined,
  CheckCircleOutlined,
} from "@mui/icons-material";

import { UIButton } from "../../exports";

// import Page1Img from "../assets/page-1.svg";

// requests imports
import { setupTwitterBusiness } from "../../../api/SocialMediaSetup";

// API imports
import { API } from "../../../Backend";

// toast notifications
import { useToastMessage } from "../../../contexts/ToastMessageContext";

const TwitterLogin = ({
  className,
  step,
  setHead,
  setStep,
  continueTo,
  skipPlatFormTo,
  TWUser,
  goToFinalStep,
  toLinkOnlyAccount,
}) => {
  const [pages, setPages] = useState([
      // {
      //   image: Page1Img,
      //   name: "Honda bikes",
      //   selected: true,
      // },
    ]),
    [btnDisable, setBtnDisable] = useState(true),
    [linkOnlyTW, setLinkOnlyTW] = useState(false),
    selectPage = (selectedIndex) => {
      setPages((prev) =>
        prev.map((page, index) =>
          selectedIndex === index
            ? { ...page, selected: !page.selected }
            : { ...page }
        )
      );
    };

  console.log(step);

  // contexts
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  // component effects
  useEffect(() => {
    const linkOnlyTW = localStorage.getItem("onlyLinkTW");
    if (linkOnlyTW) {
      setLinkOnlyTW(linkOnlyTW.toLowerCase() === "true");
    }
  }, []);

  useEffect(() => {
    if (TWUser) {
      const twUserInfo = JSON.parse(TWUser);

      const twUser = {
        image: twUserInfo.profileImgURL,
        name: twUserInfo.twScreenName,
        selected: false,
        twUserID: twUserInfo.twUserID,
        token: twUserInfo.token,
        tokenSecret: twUserInfo.tokenSecret,
        twName: twUserInfo.twName,
      };

      setPages([twUser]);
    }
  }, [TWUser]);

  useEffect(() => {
    if (pages.length) {
      setStep(1);
    }
  }, [pages, setStep]);

  useEffect(() => {
    if (pages.length) {
      if (pages[0].selected) {
        // save the twitter user info in db
        const {
          twUserID,
          name: twScreenName,
          twName,
          token,
          tokenSecret,
          image: profileImgURL,
        } = pages[0];

        const twitterSetupData = {
          twUserID,
          twScreenName,
          twName,
          token,
          tokenSecret,
          profileImgURL,
        };
        setupTwitterBusiness(twitterSetupData)
          .then((data) => {
            const { success } = data;
            if (success) {
              if (linkOnlyTW) {
                localStorage.removeItem("onlyLinkTW");
                goToFinalStep();
              } else {
                setStep(1);
                setHead("Twitter Account is Linked");
                setBtnDisable(false);
                setSuccessMessage("Twitter setup complete!");
              }
            }
          })
          .catch((error) => {
            const { message, error: err } = error;
            if (message) {
              setErrorMessage(message);
            } else if (err) {
              setErrorMessage(err);
            } else {
              setErrorMessage(error);
            }
          });
      } else {
        setBtnDisable(true);
      }
    }
  }, [pages, setHead, setStep]);

  // Event handlers
  const handleTWLoginRedirect = () => {
    // window.open(`http://localhost:5000/api/twitter/login`, "_self");
    window.open(`${API}/twitter/login`, "_self");
  };

  const ListItemImage = ({ src, alt }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => {
      setLoading(false);
    };

    return (
      <ListItemAvatar>
        {loading && <Skeleton variant="rectangular" width={50} height={50} />}
        <img
          src={src}
          alt={alt}
          onLoad={onImageLoad}
          width={loading ? 0 : 50}
          height={loading ? 0 : 50}
          style={{ maxWidth: "100%" }}
        />
      </ListItemAvatar>
    );
  };

  return (
    <div className={className}>
      {step === 0 && (
        <div className="platform-head">
          <p>
            {
              "This will allow you create, schedule & manage posts directly from easysocial."
            }
          </p>
          <a target="_blank" rel="noopener noreferrer">
            {"Vire detaiew mols"}
          </a>
        </div>
      )}
      {step === 1 && (
        <List>
          {pages.map(({ image, name, selected }, index) => (
            <Fragment key={index}>
              <ListItem sx={{ p: 0 }}>
                <ListItemButton onClick={() => selectPage(index)}>
                  <ListItemImage src={image} alt={name} />
                  <ListItemText primary={name} />
                  <ListItemIcon>
                    {selected ? (
                      <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                    ) : (
                      <CircleOutlined sx={{ color: "#24272C80" }} />
                    )}
                  </ListItemIcon>
                </ListItemButton>
              </ListItem>
              <ListItemText primary="Or Connect another twitter account" />
            </Fragment>
          ))}
        </List>
      )}
      {step === 0 && (
        <Button className="btn-twitter" onClick={handleTWLoginRedirect}>
          <Twitter sx={{ color: "#ffffff" }} />
          <span className="label">{"Sign in with Twitter"}</span>
        </Button>
      )}
      <div className="actions">
        {step === 1 && (
          <UIButton
            variant="contained"
            className="btn-next"
            style={{
              marginTop: "40px",
              marginBottom: "30px",
              marginLeft: 0,
            }}
            onClick={() => {
              setHead("Login with LinkedIn");
              continueTo("linkedin");
            }}
            disabled={btnDisable}
          >
            {"Continue to Linkedin"}
          </UIButton>
        )}
        <a className="skip" onClick={() => skipPlatFormTo("linkedin")}>
          <span className="label">{"Skip Twitter"}</span>
          <svg
            width="6"
            height="10"
            viewBox="0 0 6 10"
            fill="none"
            xmlns="//www.w3.org/2000/svg"
          >
            <path
              opacity="0.7"
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.483211 0.526985C0.221976 0.811968 0.241229 1.25477 0.526212 1.516L3.96329 4.66666L0.526212 7.81732C0.241229 8.07855 0.221976 8.52135 0.483211 8.80633C0.744446 9.09132 1.18724 9.11057 1.47223 8.84933L5.47223 5.18267C5.61687 5.05008 5.69922 4.86287 5.69922 4.66666C5.69922 4.47044 5.61687 4.28324 5.47223 4.15065L1.47223 0.483984C1.18724 0.222749 0.744446 0.242001 0.483211 0.526985Z"
              fill="#24272C"
            />
          </svg>
        </a>
      </div>
    </div>
  );
};

export default TwitterLogin;
